import React from "react";
import Footer from "../footer/footer";
import Header from "../header/header";
import { Main } from "./styles";

//    
function PageWrapper({
    pageUrl, //   
    children //  
}) {
    return (
        <>
            <Header pageUrl={pageUrl} />
            <Main>{children}</Main>
            <Footer />
        </>
    );
}

export default PageWrapper;